import '@telerik/kendo-theme-default/styles/packages/dropdowns';
import {
    Directive,
    Input,
    HostBinding,
    HostListener
} from '@angular/core';
import { SelectionService } from './selection.service';

/**
 * @hidden
 */
@Directive({
    selector: '[kendoDropDownsSelectable]'
})
export class SelectableDirective {
    @Input() public index: number;
    @Input() public multiple: boolean = false;

    private selectionService: SelectionService;

    constructor(selectionService: SelectionService) {
        this.selectionService = selectionService;
    }

    @HostBinding('class.k-state-focused')
    public get focusedClassName(): boolean {
        return this.selectionService.isFocused(this.index);
    }

    @HostBinding('class.k-state-selected')
    public get selectedClassName(): boolean {
        return this.selectionService.isSelected(this.index);
    }

    @HostBinding('attr.aria-selected') public get ariaSelected(): boolean {
        return this.selectionService.isSelected(this.index) ? true : undefined;
    }

    @HostListener('click', ['$event'])
    public onClick(event: any): void {
        event.stopPropagation();
        if (this.multiple) {
            //TODO: multiple selection (multiselect)
        } else {
            this.selectionService.change(this.index);
        }
    }
}



